import * as React from 'react';
import Resume from './components/containers/Resume';
import Todos from './components/containers/Todos';
import { Route, Switch } from 'react-router-dom';
import Header from './components/dumbs/Header';
import News from './components/smarts/News';
import 'typeface-roboto'
import withRoot from './components/smarts/ThemeSetting';

class App extends React.Component {
  public render() {
    return (
      <div>
        <Header />
        <Switch>
          <Route component={Resume} path='/resume' />
          <Route component={Todos} path='/todos' />
          <Route component={News} path='/' />
        </Switch>
      </div>
    );
  }
}

export default withRoot(App);

