import { ITheme, IThemeAction } from './../constants/Interface';
import { CHANGTHEME } from '../constants/ActionType';


export const ChangeTheme:(theme: ITheme) => IThemeAction = (theme: ITheme) => {
    return {
        type: CHANGTHEME,
        theme
    }
}


