import * as React from 'react';
import Call from '@material-ui/icons/Call';
import Cake from '@material-ui/icons/Cake';
import Email from '@material-ui/icons/Email';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import SmallList from '../dumbs/SmallList';


class Resume extends React.Component {

  public render() {
    return (
      <div style={{ padding: 20 }}>
        <Grid container={true} 
              spacing={8} 
              direction='row'
              justify='center'
              alignItems='baseline'>
          <Grid item={true} xs={12} sm={12} md={6} lg={5}>
            <Grid container={true} 
                  spacing={8}
                  direction='column'
                  justify='flex-start'>
              <Grid item={true}>
                <Paper>
                  <Typography variant='display1'>張本慶</Typography>
                  <List>
                    <ListItem>
                      <ListItemIcon><Cake /></ListItemIcon>
                      <ListItemText>Jan, 23th, 1988</ListItemText>
                    </ListItem>
                    <ListItem>
                      <ListItemIcon><Email /></ListItemIcon>
                      <ListItemText>cabal_357@hotmail.com</ListItemText>
                    </ListItem>
                    <ListItem>
                      <ListItemIcon><Call /></ListItemIcon>
                      <ListItemText>0912-559123</ListItemText>
                    </ListItem>
                  </List>
                </Paper>
              </Grid>
              <Grid item={true}>
                <Paper>
                  <Typography variant='display1'>學歷</Typography>
                  <List>
                    <ListItem>
                      <ListItemText>中興大學農藝系</ListItemText>
                    </ListItem>
                    <ListItem>
                      <ListItemText>鳳新高中</ListItemText>
                    </ListItem>
                    <ListItem>
                      <ListItemText>壽山國中</ListItemText>
                    </ListItem>
                    <ListItem>
                      <ListItemText>鼓山國小</ListItemText>
                    </ListItem>
                  </List>
                </Paper>
              </Grid>
              <Grid item={true}>
                <Paper>
                  <Typography variant='display1'>經歷</Typography>
                  <List>
                  <ListItem>
                    <ListItemText>台灣人工智慧學校</ListItemText>
                  </ListItem>
                  <ListItem>
                    <ListItemText>高雄醫學大學附設中和紀念醫院資訊室 資訊工程師</ListItemText>
                  </ListItem>
                  <ListItem>
                    <ListItemText>高雄資策會「行動應用開發程式設計師就業養成班」</ListItemText>
                  </ListItem>
                  </List>
                </Paper>
              </Grid>
            </Grid>
          </Grid>
          <Grid item={true} xs={12} sm={12} md={6} lg={7}>
            <Paper>
              <Typography variant='display1'>專長</Typography>
              <Grid container={true}>
                <Grid item={true} xs={12} sm={6} md={6} lg={6}>
                  <SmallList content={{
                          title: '程式語言', 
                          text: [
                            'Javascript',
                            'C#',
                            'Python',
                            'Golang'
                          ]}} />
                </Grid>
                <Grid item={true} xs={12} sm={6} md={6} lg={6}>
                  <SmallList content={{
                        title: '前端', 
                        text: [
                          'ES2015/ES2016',
                          'HTML5',
                          'CSS3',
                          'jQuery',
                          'Angular',
                          'React',
                          'Bootstrap',
                          'Material Design'
                        ]}} />
                </Grid>
                <Grid item={true} xs={12} sm={6} md={6} lg={6}>
                  <SmallList content={{
                        title: '後端', 
                        text: [
                          'ASP.NET MVC',
                          'ASP.NET core MVC',
                          'Node.js',
                          'Gin'
                        ]}} />
                </Grid>
                <Grid item={true} xs={12} sm={6} md={6} lg={6}>
                  <SmallList content={{
                        title: '資料庫', 
                        text: [
                          'Oracle',
                          'Mongo',
                          'MySQL',
                          'MSSQL'
                        ]}} />
                </Grid>
              </Grid>
            </Paper>
          </Grid>
        </Grid>
      </div>
    );
  }
}

export default Resume;



