import * as React from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

interface IContect {
    title: string;
    text: string[];
}

interface IProps {
    content: IContect
}

class SmallList extends React.Component<IProps> {

    public render() {
        return (
            <List>
                <ListItem>
                    <ListItemText 
                        primary={this.props.content.title} 
                        secondary={
                            <List>
                                {
                                    this.props.content.text.map((t: string, i: number) => {
                                        return <ListItem key={i}><ListItemText primary={t} /></ListItem>
                                    })
                                }
                            </List>
                        } />
                </ListItem>
            </List>
        );
    }
}

export default SmallList;



