import { ITheme, IThemeAction } from './../constants/Interface';
import { Reducer } from 'redux';
import { CHANGTHEME } from '../constants/ActionType';

const initTheme:ITheme={
    newtheme:'light'
}

export const Theme:Reducer<ITheme,IThemeAction> = (state:ITheme=initTheme, action:IThemeAction)=>{
  switch (action.type) {
    case CHANGTHEME:
      return { newtheme: action.theme.newtheme }
    default:
      return state
  }
}


