import { ITodo, ITodoAction } from './../constants/Interface';
import { Reducer } from 'redux';
import { GETTODOS, GETTODO, EDITETODO, CREATETODO, DELETETODO } from '../constants/ActionType';

const initTodos:ITodo[]=[
  {
    id:0,
    time: "2018-09-12T02:02",
    title:"123",
    content: "gggggggg",
    completed:false
  },{
    id:1,
    time: "2018-09-12T02:02",
    title:"456",
    content: "gggggggg",
    completed:false
  },{
    id:2,
    time: "2018-09-12T02:02",
    title:"789",
    content: "gggggggg",
    completed:false
  },{
    id:3,
    time: "2018-09-12T02:02",
    title:"A01",
    content: "gggggggg",
    completed:false
  },{
    id:4,
    time: "2018-09-12T02:02",
    title:"A02",
    content: "gggggggg",
    completed:false
  },{
    id:5,
    time: "2018-09-12T02:02",
    title:"A03",
    content: "gggggggg",
    completed:false
  },{
    id:6,
    time: "2018-09-12T02:02",
    title:"A04",
    content: "gggggggg",
    completed:false
  }
]
export const Todos:Reducer<ITodo[],ITodoAction> = (state:ITodo[]=initTodos,action:ITodoAction)=>{
  switch (action.type) {
    case GETTODOS:
      return [...state]
    case GETTODO:
      return state.filter((x)=> x.id === (action as any).todo.id)
    case EDITETODO:
      return state.map((x)=>x.id === (action as any).todo.id?{...(action as any).todo}:x)
    case CREATETODO:
      (action as any).todo.id = state.map((x)=>x.id).reduce((p,c)=> (c>=p)?c:p,0) + 1;
      return [...state,(action as any).todo]
    case DELETETODO:
      return state.filter((x)=>{
        return x.id !== (action as any).todo.id
      })
    default:
      return state
  }
}





