import * as React from 'react';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { ITodo, ITodoAction } from '../../constants/Interface';
import { TodoDefault } from '../../constants/DefaultValue';
import action from '../../actions';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';


interface IProps extends ITodo{
  delete:(todo:ITodo)=>ITodoAction;
  edit:(todo:ITodo)=>ITodoAction;
}

class TodoCard extends React.Component<IProps,React.ComponentState> {
  public state ={
    isEdite:false,
    ...TodoDefault
  }

  public componentWillMount(){
    this.toggleEdite = this.toggleEdite.bind(this)
    this.onEdit = this.onEdit.bind(this)
    this.onDelete = this.onDelete.bind(this)
    this.onConfirm = this.onConfirm.bind(this)
    this.onCancel = this.onCancel.bind(this)
    this.reSetState = this.reSetState.bind(this)

    this.reSetState()
  }

  public reSetState(){
    this.setState({
      id:this.props.id,
      title:this.props.title,
      content:this.props.content,
      time:this.props.time,
      completed:this.props.completed,
    })
  }

  public toggleEdite(){
    this.setState({isEdite:!this.state.isEdite})
  }

  public handleChange = (name:string) => (event:React.ChangeEvent<HTMLTextAreaElement>) => {
    this.setState({
      [name]:event.currentTarget.value,
    })
  }

  public onEdit(){
    this.toggleEdite()
    this.reSetState()
  }

  public onDelete(){
    this.props.delete({
      ...TodoDefault,
      id:this.props.id,
    })
  }

  public onConfirm(){
    this.props.edit({
      id:this.props.id,
      time:this.state.time,
      title:this.state.title,
      content:this.state.content,
      completed:this.state.completed,
    })
    this.toggleEdite()
  }

  public onCancel(){
    this.setState({isEdite:false})
    this.reSetState()
  }

  public render() {
    return (
      <Grid item={true} xs={12} sm={6} md={4} lg={3}>
        <Card>
          {
            this.state.isEdite?
            <CardContent>
              <TextField
                id="datetime-local"
                label="Next appointment"
                type="datetime-local"
                defaultValue={this.props.time}
                InputLabelProps={{
                  shrink: true,
                }}
                onChange={ this.handleChange('time') }
                margin="normal"
              />
              <TextField
                label="New Title"
                defaultValue={this.props.title}
                InputLabelProps={{
                  shrink: true,
                }}
                onChange={ this.handleChange('title') }
                margin="normal"
                fullWidth={true}
              />
              <TextField
                label="New Content"
                defaultValue={this.props.content}
                InputLabelProps={{
                  shrink: true,
                }}
                onChange={ this.handleChange('content') }
                margin="normal"
                multiline={true}
                fullWidth={true}
              />
            </CardContent>
            :
            <CardContent>
              <Typography color='textSecondary'>
                { (new Date(this.props.time)).toLocaleString() }
              </Typography>
              <Typography color='textPrimary'>
                {this.props.title}
              </Typography>
              <Typography color='textSecondary'>
                {this.props.content}
              </Typography>
            </CardContent>
          }
          {
            this.state.isEdite?
            <CardActions>
              <Button size="small" color="primary" onClick={this.onConfirm}>
                Confirm
              </Button>
              <Button size="small" color="primary" onClick={this.onCancel}>
                Cancel
              </Button>
            </CardActions>
            :
            <CardActions>
              <Button size="small" color="primary" onClick={this.onEdit}>
                Edit
              </Button>
              <Button size="small" color="primary" onClick={this.onDelete}>
                Delete
              </Button>
            </CardActions>
          }
        </Card>
      </Grid>
    );
  }



  


}

function mapDispatchToProps(dispatch: Dispatch) {
  return bindActionCreators({
      edit: action.todos.EditeTodo,
      delete: action.todos.DeleteTodos
  }, dispatch)
}

export default connect(null,mapDispatchToProps)(TodoCard);



