import * as React from 'react';
import { connect } from 'react-redux';
import { ITodo } from '../../constants/Interface';
import TodoCard from '../smarts/Todocard';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import Typography from '@material-ui/core/Typography';


class Todos extends React.Component<IMapStateToProps, React.ComponentState> {
    public render() {
        return (
            <div>
                <Grid container={true} direction='column'>
                    <Grid item={true} style={{margin:'auto'}}>
                        <Typography variant='display1'>
                            ToDoList
                        </Typography>
                    </Grid>
                    <Grid item={true} >
                        <Grid container={true} spacing={16} wrap='wrap' style={{margin:'auto'}} >
                        {
                            this.props.Todos.map((x: ITodo, i: number) => {
                                return <TodoCard key={i} {...x} />
                            })
                        }
                        </Grid>
                    </Grid>         
                    <Grid item={true} style={{margin:'auto'}} >
                        <Button variant="fab" >
                            <AddIcon />
                        </Button>
                    </Grid>
                </Grid>
            </div>
        );
    }
}

interface IMapStateToProps {
    Todos: ITodo[]
}

function mapStateToProps(state: any) {
    return {
        Todos: state.Todos
    }
}


export default connect<IMapStateToProps, {}>(mapStateToProps, {})(Todos);



