import * as React from 'react';
import { Link } from 'react-router-dom';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';
import Paper from '@material-ui/core/Paper';
import Popper from '@material-ui/core/Popper';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';


class Header extends React.Component {

  public state = {
    open: false,
    anchorEl: null
  };

  public bindAnchor = (node:any)=>{
    this.state.anchorEl = node;
  }

  public render() {
    return (
      <div>
        <AppBar position="static" color='primary'>
          <Toolbar variant="dense">
            <IconButton color="inherit" aria-label="Menu" onClick={this.handleToggle} buttonRef={this.bindAnchor} >
              <MenuIcon />
            </IconButton>
            <Typography variant="title" color="inherit">
              Frohmanthenoob
            </Typography>
          </Toolbar>
        </AppBar>
        <Popper open={this.state.open} anchorEl={this.state.anchorEl}>
          <Paper>
            <ClickAwayListener onClickAway={this.handleClose}>
              <MenuList>
                <Link to={`/`}><MenuItem onClick={this.handleClose} >Home</MenuItem></Link>
                <Link to={`/resume`}><MenuItem onClick={this.handleClose} >Resume</MenuItem></Link>
                <Link to={`/todos`}><MenuItem onClick={this.handleClose} >Todos</MenuItem></Link>
              </MenuList>
            </ClickAwayListener>
          </Paper>
        </Popper>
      </div>
    );
  }

  private handleToggle = () => {
    this.setState(state => ({ open: !this.state.open }));
  };

  private handleClose = (event:any) => {
    this.setState({ open: false });
  };

}

export default Header;



